$font-stack:    Helvetica, sans-serif;
$erie-color: #4891E7;
$mead-color: #90CC54;
$cv-color:#9681bf;

body {
  font: 100% $font-stack;

}

.alert-box
{
	z-index:2005 !important;
}
.view-featured-sliders .readmore a,
.field-name-field-button a {
	background:$cv-color;


border-radius: 2px;
text-align:center;
padding:6px;
color:white;
margin:10px 0;
float:left;

&:hover {
	background: darken($cv-color, 13.5%);
}	
}
.flex-direction-nav
{
	display:none;
}

.pane-featured-sliders h2
{
	display:none;
}
main
{
	-webkit-box-shadow: 0px 9px 5px 8px rgba(217,217,217,1);
	-moz-box-shadow: 0px 9px 5px 8px rgba(217,217,217,1);
	box-shadow: 0px 9px 5px 8px rgba(217,217,217,1);
	padding-top:20px;
	padding-bottom:20px;
}



//Mead Specific Colors
body.section-mead
{
	.pane-ads,  .pane-block
	{
		border-color:$mead-color;
	}
	.view-featured-sliders .readmore a, .field-name-field-button a
	{
		background-color:$mead-color;
	}
}

//Erie Specific Colors
body.section-erie
{
	.pane-ads,  .pane-block
	{
		border-color:$erie-color;
	}
	.view-featured-sliders .readmore a, .field-name-field-button a
	{
		background-color:$erie-color;
	}
}

.field-name-field-authors
{
	float:left;
	display:inline;
	padding:5px;
	border:2px solid #e3e3e3;
	margin:10px;
}